
.tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: transparent;

}


.tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
}


.tab button:hover {
    background-color: #ddd;
}


.tab button.active {
    background-color: white;
}


.tabcontent {
    padding: 6px 12px;
    border: 1px solid #ccc;
    border-top: none;
}